"use client";

import { useState } from "react";
import Link from "next/link";
import {
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  Button,
  Menu,
  MenuItem,
} from "@mui/material";
import MenuIcon from "@mui/icons-material/Menu";

const Navbar = () => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const open = Boolean(anchorEl);
  const [activeSection, setActiveSection] = useState("home");

  const handleMenu = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const navItems = [
    { name: "About", path: "/about" },
    { name: "Admissions", path: "/admissions" },
    { name: "Contact", path: "/contact" },
    { name: "Courses", path: "/courses" },
  ];

  return (
    <nav className="bg-blue-600 text-white p-4 shadow-md">
      <div className="container mx-auto flex justify-between items-center">
        <Link href="/" className="text-2xl font-bold">
          AIMSR
        </Link>

        {/* Desktop Links */}
        <ul className="hidden md:flex space-x-4">
          <li>
            <a
              href="/about"
              className={`hover:underline ${activeSection === "about" ? "border-b-2" : ""}`}
              onClick={() => setActiveSection("about")}
            >
              About
            </a>
          </li>
          <li>
            <a
              href="/courses"
              className={`hover:underline ${activeSection === "courses" ? "border-b-2" : ""}`}
              onClick={() => setActiveSection("courses")}
            >
              Courses
            </a>
          </li>
          <li>
            <Link
              href="/admissions"
              className={`text-white hover:underline ${activeSection === "admissions" ? "border-b-2" : ""}`}
              onClick={() => setActiveSection("admissions")}
            >
              Admissions
            </Link>
          </li>
          <li>
            <a
              href="/contact"
              className={`hover:underline ${activeSection === "contact" ? "border-b-2" : ""}`}
              onClick={() => setActiveSection("contact")}
            >
              Contact
            </a>
          </li>
        </ul>

        {/* Mobile Menu */}
        <div className="md:hidden">
          <IconButton
            edge="start"
            color="inherit"
            aria-label="menu"
            onClick={handleMenu}
          >
            <MenuIcon />
          </IconButton>
          <Menu
            id="menu-appbar"
            anchorEl={anchorEl}
            anchorOrigin={{
              vertical: "top",
              horizontal: "right",
            }}
            keepMounted
            transformOrigin={{
              vertical: "top",
              horizontal: "right",
            }}
            open={open}
            onClose={handleClose}
          >
            <Link
              href="/about"
              style={{ textDecoration: "none", color: "inherit" }}
              onClick={handleClose}
            >
              <MenuItem>About</MenuItem>
            </Link>
            <Link
              href="/admissions"
              style={{ textDecoration: "none", color: "inherit" }}
              onClick={handleClose}
            >
              <MenuItem>Admissions</MenuItem>
            </Link>
            <Link
              href="/contact"
              style={{ textDecoration: "none", color: "inherit" }}
              onClick={handleClose}
            >
              <MenuItem>Contact</MenuItem>
            </Link>
            <Link
              href="/courses"
              style={{ textDecoration: "none", color: "inherit" }}
              onClick={handleClose}
            >
              <MenuItem>Courses</MenuItem>
            </Link>
          </Menu>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
